.natural-language-page {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  background: #f8fafc;
  min-height: 100vh;
}
.natural-language-page .main-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  align-items: start;
}
.natural-language-page .main-content .chat-section {
  min-height: 600px;
}
.natural-language-page .main-content .sidebar {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.natural-language-page .error-state {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.natural-language-page .error-state .error-content {
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  max-width: 400px;
  margin: 1rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.natural-language-page .error-state .error-content .error-icon {
  color: #ef4444;
  margin-bottom: 1rem;
}
.natural-language-page .error-state .error-content .error-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.natural-language-page .error-state .error-content .error-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0 0 1.5rem 0;
  line-height: 1.5;
}
.natural-language-page .error-state .error-content .retry-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border: none;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}
.natural-language-page .error-state .error-content .retry-button:hover {
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  transform: translateY(-1px);
}
.natural-language-page .error-state .error-content .retry-button:active {
  transform: translateY(0);
}
.natural-language-page .error-state .error-content .retry-button svg {
  width: 1rem;
  height: 1rem;
}

@media (max-width: 1024px) {
  .natural-language-page .main-content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .natural-language-page .main-content .sidebar {
    order: -1;
  }
}
@media (max-width: 768px) {
  .natural-language-page {
    padding: 1rem;
  }
  .natural-language-page .main-content {
    gap: 1rem;
  }
  .natural-language-page .main-content .chat-section {
    min-height: 500px;
  }
  .natural-language-page .error-state .error-content {
    padding: 1.5rem;
    margin: 0.5rem;
  }
  .natural-language-page .error-state .error-content .error-title {
    font-size: 1.125rem;
  }
  .natural-language-page .error-state .error-content .error-description {
    font-size: 0.8rem;
  }
  .natural-language-page .error-state .error-content .retry-button {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
}
